html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
}

a {
  display: inline-block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

:root {
  --Pink: hsl(322, 100%, 66%);
  --white: hsl(0, 0%, 100%);
  --Very-Pale-Cyan: hsl(193, 100%, 96%);
  --Very-Dark-Cyan: hsl(192, 100%, 9%);
  --Grayish-Blue: hsl(208, 11%, 55%);
}

:root {
  --ff-sans: "Open Sans", sans-serif;
  --ff-poppins: "Poppins", sans-serif;
}

.container {
  width: min(90%, 90rem);
  margin-inline: auto;
}
.container--narrow {
  width: min(90%, 90rem);
  margin-inline: auto;
}
@media (max-width: 43.74875em) {
  .container--narrow {
    width: min(80%, 90rem);
  }
}
.container--large {
  width: min(90%, 77.5rem);
  margin-inline: auto;
}

@media (min-width: 43.75em) {
  .flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
  }
}
.flex__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 43.75em) {
  .flex--reverse {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    gap: 6rem;
  }
}
@media (min-width: 56.25em) {
  .flex__footer {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
}

body {
  font-family: var(--ff-sans);
  font-weight: 400;
}
@media (max-width: 43.74875em) {
  body {
    text-align: center;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  font-family: var(--ff-poppins);
}

p {
  margin-top: 0;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: inherit;
}

.navbar__link--button {
  font-weight: 600;
  color: hsl(0deg, 0%, 10%);
  font-size: 0.875rem;
}
@media (max-width: 43.74875em) {
  .navbar__link--button {
    font-size: 0.75rem;
  }
}

@media (min-width: 43.75em) {
  .hero__title {
    font-size: 2rem;
  }
}
@media (min-width: 56.25em) {
  .hero__title {
    font-size: 2.5rem;
  }
}

.narrower {
  width: min(90%, 31.25rem);
}

.button {
  font-weight: 700;
  color: var(--Very-Pale-Cyan);
}
.button--small {
  font-size: 0.8125rem;
}
@media (max-width: 43.74875em) {
  .button--small, .button--big {
    font-size: 0.75rem;
  }
}

@media (max-width: 43.74875em) {
  .card__title {
    font-size: 1.25rem;
  }
}
.card__text {
  color: var(--Grayish-Blue);
}

.button {
  background-color: var(--Pink);
  padding: 1.375em 4.5em;
  border-radius: 6.25rem;
  box-shadow: 0px 8px 24px hsla(322deg, 100%, 66%, 0.2);
  transition: all 0.5s ease-out;
}

.navbar__link--button {
  padding: 0.5em 2.25em;
  background-color: var(--white);
  box-shadow: 0px 8px 24px hsla(210deg, 8%, 62%, 0.2);
  border-radius: 6.25rem;
  transition: all 0.5s ease-out;
}
@media (min-width: 43.75em) {
  .navbar__link--button {
    padding: 1em 4em;
  }
}

.button:hover,
.navbar__link--button:hover {
  opacity: 0.7;
  transform: scale(1.1, 1.1);
}

.card {
  padding: 2em;
  background-color: var(--white);
  box-shadow: 0px 8px 24px hsla(210deg, 8%, 62%, 0.2);
  border-radius: 0.75rem;
}
@media (min-width: 43.75em) {
  .card {
    padding-left: 4.5em;
  }
}
@media (min-width: 43.75em) {
  .card__body--space {
    padding-left: 4rem;
  }
}
@media (min-width: 31.25em) and (max-width: 43.74875em) {
  .card__image {
    display: flex;
    justify-content: center;
  }
  .card__image > * {
    max-width: 25rem;
  }
}
@media (max-width: 43.74875em) {
  .card__image {
    margin-bottom: 4em;
  }
}
.card--small {
  text-align: center;
  position: relative;
  top: 10.25rem;
  max-width: 37.5rem;
  margin-inline: auto;
}

.card + .card {
  margin-top: 3rem;
}

.reveal {
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: 2s all ease;
}

.reveal.active {
  transform: translateY(0);
  opacity: 1;
}

.footer {
  padding-block: 8.25em 3em;
  background-color: var(--Very-Dark-Cyan);
  color: var(--Very-Pale-Cyan);
  text-align: left;
}
.footer__logo {
  margin-bottom: 2rem;
  mix-blend-mode: multiply;
}
.footer__phone::before {
  content: url("../../../images/icon-phone.svg");
}
.footer__email::before {
  content: url("../../../images/icon-email.svg");
}
.footer__location::before {
  content: url("../../../images/icon-location.svg");
}
.footer__location {
  max-width: 31.25rem;
}
@media (max-width: 56.24875em) {
  .footer__email {
    margin-bottom: 3rem;
  }
}
.footer__phone, .footer__email, .footer__location {
  display: flex;
  gap: 1rem;
}
.footer__link ~ .footer__link {
  margin-top: 1rem;
}
@media (max-width: 43.74875em) {
  .footer__link:nth-of-type(3) {
    margin-bottom: 1rem;
  }
}
.footer__link {
  transition: all 0.3s ease-out;
}
.footer__link:hover {
  transform: scale(1.1, 1.1);
  text-decoration: underline;
}

.header {
  background-color: var(--Very-Pale-Cyan);
  background-image: url("../../../images/bg-hero-mobile.svg");
  padding-bottom: 3em;
}
@media (min-width: 43.75em) {
  .header {
    background-image: url("../../../images/bg-hero-desktop.svg");
  }
}

.hero__body {
  margin-bottom: 2em;
  animation: text 1s ease-out;
}
@media (min-width: 43.75em) {
  .hero__body {
    max-width: 40%;
  }
}
.hero__image {
  animation: image 1s ease-out;
}
@media (min-width: 43.75em) {
  .hero__image {
    max-width: 55%;
  }
}
.hero__text {
  margin-bottom: 2.25em;
}
@media (max-width: 43.74875em) {
  .hero__button {
    margin-bottom: 2em;
  }
}

@keyframes text {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes image {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.navbar {
  padding-block: 2rem;
}
@media (max-width: 43.74875em) {
  .navbar {
    margin-bottom: 3em;
  }
}
.navbar__logo {
  animation: logo 1s ease-in-out;
}
.navbar__image {
  width: 6.25rem;
}
@media (min-width: 43.75em) {
  .navbar__image {
    width: 12.5rem;
  }
}

@keyframes logo {
  100% {
    transform: rotate(3turn);
  }
}
.main {
  padding-block: 6em;
}